export default class ArgType {
  private static GetCategory(category?: string) {
    return category ? { category: category } : undefined;
  }

  public static Category(category: string) {
    return {
      table: ArgType.GetCategory(category),
    };
  }

  public static Range(min: number, max: number, step: number, category?: string) {
    return {
      control: { type: 'range', min: min, max: max, step: step },
      table: ArgType.GetCategory(category),
    };
  }

  public static Select(options: string[], category?: string) {
    return {
      options: options,
      control: {
        type: 'select',
      },
      table: ArgType.GetCategory(category),
    };
  }

  public static Radio(options: string[], category?: string) {
    return {
      options: options,
      control: { type: 'radio' },
      table: ArgType.GetCategory(category),
    };
  }

  public static File(accept: '.png' | '.json', category?: string) {
    return {
      control: { type: 'file', accept: accept },
      table: ArgType.GetCategory(category),
    };
  }

  public static Object(category?: string) {
    return {
      control: 'object',
      table: ArgType.GetCategory(category),
    };
  }

  public static Invoke<T>(args?: T, argTypes?: Partial<Record<keyof T, any>>, category?: string) {
    return {
      control: {
        type: 'invoke',
        args: args,
        argTypes: args && argTypes ? { ...argTypes } : undefined,
      },
      table: ArgType.GetCategory(category),
    };
  }
}
